import { useState, useRef, useEffect } from 'react'
import { Home, Person, Code, ContactMail, FolderOpen } from '@material-ui/icons';
import Link from 'next/link';

const iconProp = { fontSize: '38px', verticalAlign: 'middle' }

const DynamicNavigation = ({ isSticky, pageDetails: { back, next, title } }) => {
    return (
        <>
        <div className="nav_sticky navigation-links">
            <div className="navigation-links__button">
                <Link href={back} passHref> 
                    <span className="navigation-links__text">
                        Back 
                    </span>
                    <span className="navigation-links__icon">
                        <Home style={iconProp} />
                    </span> 
                </Link> 
            </div>           
            <div className={`navigation-links__page ${!isSticky ? 'disappear' : 'appear'}`}>
                {title}
            </div>
            <div className="navigation-links__button">
                <Link  href={next} passHref> 
                    <span className="navigation-links__text">
                        {next}
                    </span>
                    <span className="navigation-links__icon">
                        <Home style={iconProp} />
                    </span> 
                </Link>
            </div>
        </div>
        </>
    )
}

export default DynamicNavigation